
.statistics{
margin-top: 32px;
margin-bottom: 32px;

}

.upload {
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    background-color: rgba(255, 255, 255, 0.902);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}

.title{
color: rgb(5, 65, 103);
padding-top: 20px;
padding-bottom: 20px;
text-transform: uppercase;
}

.stat_list{
    display: flex;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
}

.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    justify-content: center;
    width: 59px;
    height: 60px;
    background-color: rgb(137, 198, 226);
    border: 0.5px solid rgb(10, 99, 154);
}

.item:nth-child(2n) {
    background-color: rgb(84, 185, 231);
}

.percentage{
    font-weight: 600;
    font-size: 20px;
}