p {
    margin: 0;
}



.profile{
    background-color: rgb(155, 208, 231);
    display: block;
    width: 300px;
    height: 320px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 32px;
    padding-bottom: 32px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
}

.description {  
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px
}

.avatar{
    width: 150px;
    height: 150px;
    border-radius: 50%
}

.name{
    font-size: 24px;
    font-weight: 600;
}

.tag{
    font-weight: 500;  
}


.stats{
    display: flex;
    justify-content: center;
    gap: 40px;
    padding: 10px;
}

.label{
    font-weight: 600;
}


.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
}